h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

body {
  margin: 0;
  background-color: #eee;
}

ul {
  margin: 0;
  padding-left: 20px;
}

button {
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: #0000;
  border: 0;
  cursor: pointer;
  outline: none;
}

input[type='text'] {
  width: 140px;
  height: 40px;
  padding-left: 10px;
  border: 0;
  border-radius: 10px;
  outline: none;
}

input[type='text']::placeholder {
  color: #999;
}

::-webkit-scrollbar {
  width: 10px;
}

a {
  text-decoration: none;
  color: black;
}

::-webkit-scrollbar-thumb {
  background-color: white;
  background-clip: padding-box;
  border: 1px solid transparent;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: #0000;
}
