.container {
  display: grid;
  grid-template-columns: 1fr 150px;
  grid-template-rows: 40px 1fr;
  gap: 20px;
  padding: 20px;

  height: 95vh;
}

.top {
  display: grid;
  grid-template-columns: auto 1fr 150px;
  gap: 20px;
  align-items: center;

  grid-area: 1/1/2/3;
}

.theme {
  justify-self: start;
}

.searchBar {
  justify-self: end;
}

.main {
  padding: 20px;
  background-color: white;
  border-radius: 10px;
}

.nav {
  padding: 20px;
  background-color: white;
  border-radius: 10px;
}

.list:hover {
  text-decoration: underline;
}
